@tailwind base;
@tailwind components;
@tailwind utilities;

.font-link {
    font-family: 'Barlow', sans-serif;
  }

.carousel {
    width: 100%;
    height: 100vh;
}

.carouselInner {
    height: 100%;
    width: 100%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    object-fit: cover;
}
.carDiv{
    height: 100vh;
    width:100%;
    position: absolute;
    top: 0;
    backdrop-filter: blur(3px);
    display: flex;
    color: white;
    overflow-wrap: normal;
    
}
.carText{
  margin-left: 50px;
  padding: 30px;
  align-items: center;
  width: 2000px;
  color: #DEF2F1;
}
    


    





